.search{position:relative}.search--animate{margin:9px 0 0 12px}.search__button{position:absolute;top:0;right:0;height:100%;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;padding:0 8px;border:none;cursor:pointer;background-color:rgba(0,0,0,0)}.search__button svg{fill:#a3a3a3}.search__input{background:#fff;font-family:TTCommons-Regular,sans-serif;font-size:18px;line-height:21px;border:2px solid #a5a5a5;-webkit-box-sizing:border-box;box-sizing:border-box;border-radius:4px;min-height:35px;min-width:320px;padding:7px 8px 5px 12px;-webkit-transition:all .25s ease-out;-o-transition:all .25s ease-out;transition:all .25s ease-out}@media (max-width:480px){.search__input{min-width:240px}}.search__input--black{background:#121212;font-family:TTCommons-Regular,sans-serif;font-size:18px;line-height:21px;color:#888;min-width:auto;width:100%;border:2px solid #323232}.search__input--black::-webkit-input-placeholder{font-family:TTCommons-Regular,sans-serif;font-size:18px;line-height:21px;color:#888}.search__input--black:-moz-placeholder,.search__input--black::-moz-placeholder{font-family:TTCommons-Regular,sans-serif;font-size:18px;line-height:21px;color:#888}.search__input--black:-ms-input-placeholder{font-family:TTCommons-Regular,sans-serif;font-size:18px;line-height:21px;color:#888}.search__animate{position:relative;width:40px;opacity:0;visibility:hidden;-webkit-transition:all .25s ease-out;-o-transition:all .25s ease-out;transition:all .25s ease-out}.search__animate--active{width:320px;opacity:1;visibility:visible}@media (max-width:991px){.search__animate--active{width:240px}}.search__icon{position:absolute;right:2px;top:2px;width:36px;height:33px;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-align:center;-ms-flex-align:center;align-items:center;cursor:pointer}.search__icon--active{display:none}.search__icon svg{fill:#888}