:root{--white-color:#fff;--black-color:#222;--green-color:#219653;--blue-color:#7198cb}::-moz-selection{background:#ccc;color:#4f4c66}::selection{background:#ccc;color:#4f4c66}*,:after,:before{-webkit-box-sizing:border-box;box-sizing:border-box;-webkit-tap-highlight-color:transparent}*{margin:0;padding:0}html{overflow-x:hidden;height:100%}body{font-family:TTCommons-Regular,sans-serif;font-size:18px;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;text-shadow:none;margin:0;min-width:320px;min-height:100%;background:#121212}body,main{position:relative}main.main-sidebar{margin-left:260px;padding-left:88px;padding-right:40px}@media (max-width:1780px){main.main-sidebar{padding-left:40px}}@media (max-width:1720px){main.main-sidebar{padding-left:25px;padding-right:25px}}@media (max-width:1199px){main.main-sidebar{margin-left:200px}}@media (max-width:640px){main.main-sidebar{margin-left:0;padding-left:15px;padding-right:15px}}@media (max-width:360px){main.main-sidebar{padding-left:10px;padding-right:10px}}.icon-logout{display:inline-block;width:22px;height:22px;background-image:url(/src/img/icons/logout.png);background-repeat:no-repeat;background-position:50%}a{text-decoration:none}button{cursor:pointer}ul{list-style:none}input{outline:none}.overlay{position:fixed;top:0;left:0;width:100%;height:100%;background:rgba(18,18,18,.5);z-index:800;-webkit-transition:all .15s ease-out;-o-transition:all .15s ease-out;transition:all .15s ease-out;opacity:0;visibility:hidden}.overlay--active{opacity:1;visibility:visible}.icon-plus{position:relative;width:20px;height:20px;border:2px solid #fff;border-radius:50%}.icon-plus,.icon-plus:after,.icon-plus:before{display:block;-webkit-transition:all .2s ease-out;-o-transition:all .2s ease-out;transition:all .2s ease-out}.icon-plus:after,.icon-plus:before{content:"";position:absolute;left:50%;top:50%;-webkit-transform:translate(-50%,-50%);-ms-transform:translate(-50%,-50%);transform:translate(-50%,-50%);background:#fff}.icon-plus--black{border:2px solid #333}.icon-plus--black:after,.icon-plus--black:before{background:#333}.icon-plus:before{width:8px;height:2px}.icon-plus:after{width:2px;height:8px}.link{position:relative;display:inline-block;color:#595959;-webkit-transition:all .2s ease-out;-o-transition:all .2s ease-out;transition:all .2s ease-out;font-size:15px}@media (min-width:768px){.link{font-size:calc(15px + (300vw - 2304px)/256)}}@media (min-width:1024px){.link{font-size:18px}}.link:before{position:absolute;bottom:4px;left:0;content:"";width:100%;height:1px;background:#595959;-webkit-transition:all .15s ease-out;-o-transition:all .15s ease-out;transition:all .15s ease-out}.link:hover{color:#888}.link:hover:before{background:#888}.link-white{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;font-family:TTCommons-DemiBold,sans-serif;font-size:20px;line-height:23px;color:#fff}@media (max-width:1720px){.link-white{font-size:16px;line-height:21px}}.link-white span{margin-left:4px}.flex--column{-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column}.profile-name{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:end;-ms-flex-align:end;align-items:flex-end;margin-bottom:16px}.height-100{height:60px}@media (min-width:480px){.height-100{height:calc(60px + (4000vw - 19200px)/511)}}@media (min-width:991px){.height-100{height:100px}}.chart{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between;max-width:1508px;width:100%;padding-top:50px;padding-bottom:75px}@media (max-width:1720px){.chart{padding-top:40px;padding-bottom:40px}}@media (max-width:1440px){.chart{-webkit-box-align:center;-ms-flex-align:center;align-items:center}}.offset-24{margin-bottom:24px}.menu-view a{display:inline-block;font-size:18px;color:#445aa1;-webkit-transition:all .15s ease-out;-o-transition:all .15s ease